import { ConnectButton as RainbowConnectButton } from "@rainbow-me/rainbowkit";
import { useCallback, useState, useEffect } from "react";
import { useAccount, useNetwork, useSwitchNetwork, useContract, useSigner } from "wagmi";
import { MerkleTree } from "merkletreejs";
import { keccak256 } from "@ethersproject/keccak256";
import {
    contractAbi,
    contractAddress,
    proof,
    resolverAddress,
    targetChainId,
} from "../constants";

const Main = () => {
    const { address } = useAccount();
    const { chain } = useNetwork();
    const { data: signer } = useSigner();
    const { switchNetwork } = useSwitchNetwork();
    const [name, setName] = useState('');
    const [isNameAvailable, setIsNameAvailable] = useState(false);
    const [isChecked, setIsChecked] = useState(false);
    const [isCommiting, setIsCommiting] = useState(false);
    const [params, setParams] = useState([]);
    const [contractAddr, setContractAddr] = useState(contractAddress);
    const [resolverAddr, setResolverAddr] = useState(resolverAddress);
    const [merkleProof, setMerkleProof] = useState(proof);
    const [openSettings, setOpenSettings] = useState(false);
    const [registration, setRegistration] = useState();
    const [chainId, setChainId] = useState(targetChainId);
    
    const isWrongNetwork = chain && chain.id !== chainId;
    const isSearchDisabled = !Boolean(name);
    const contract = useContract({
        addressOrName: contractAddr,
        contractInterface: contractAbi,
        signerOrProvider: signer,
    });

    const handleSwitchNetwork = useCallback(() => {
        switchNetwork(chainId);
    }, [switchNetwork, chainId]);

    const handleNameChange = useCallback((e) => {
        setName(e.target.value);
        setIsChecked(false);
        setRegistration(undefined);
    }, []);

    const handleAddressChange = useCallback((e) => {
        const newAddress = e.target.value;
        if (!newAddress) return;
        setContractAddr(newAddress);
    }, []);
    const handleResolverAddressChange = useCallback((e) => {
        const newAddress = e.target.value;
        if (!newAddress) return;
        setResolverAddr(newAddress);
    }, []);
    const handleChainIdChange = useCallback((e) => {
        const newChainId = e.target.value;
        if (!newChainId) return;
        setChainId(Number(newChainId));
    }, []);
    
    const handleProofChange = useCallback((e) => {
      console.log("proof change")
        const value = e.target.value;
        if (!value || value === 'null') return;
        const newProof = JSON.parse(`${value.replaceAll("'", '"')}`);
        setMerkleProof(newProof);
    }, []);

    const handleToggleSettings = () => setOpenSettings(!openSettings);

    const handleSearch = useCallback(async () => {
        if (isSearchDisabled || !contract) return;

        try {
            const newCheck = await contract.available(String(name));
            setIsNameAvailable(newCheck);
            setIsChecked(true)
        } catch (error) {
            console.log({ error });
        }
    }, [contract, name, isSearchDisabled]);

    const handleRegister = useCallback(async () => {
        if (isCommiting || isSearchDisabled || !params.length) return;
        try {
            const trxResp = await contract.registerWithConfig(...params);
            await trxResp.wait();
            setParams([]);
            setRegistration({ success: true });
        } catch (error) {
            console.log('QA: ERROR', { error });
            setRegistration({ success: false });
        }
    }, [contract, isCommiting, isSearchDisabled, params]);

    const handleCommit = useCallback(async () => {
        if (!isNameAvailable) return;

        const duration = 2592000;
        // Generate a random value to mask our commitment
        const random = new Uint8Array(32);
        crypto.getRandomValues(random);
        const salt = "0x" + Array.from(random).map(b => b.toString(16).padStart(2, "0")).join("");
        // Submit our commitment to the smart contract
        const commitment = await contract.makeCommitmentWithConfig(name, address, salt, resolverAddr, address);
        await contract.commit(commitment, merkleProof);
        // Add 10% to account for price fluctuation; the difference is refunded.
        const price = await contract.rentPrice(name, duration);
        // const valueToSend = price.mul(11).div(10);
        const valueToSend = price;

        setIsCommiting(true);
        setParams([name, address, duration, salt, resolverAddr, address, { value: valueToSend, gasLimit: 800000 }]);
        // Wait 60 seconds before registering
        setTimeout(async () => {
            setIsCommiting(false);
        }, 60000);
    }, [address, contract, name, isNameAvailable, merkleProof, resolverAddr]);

    useEffect(() => {
      if (!address) return;
      const allowlist = [
        "0x3bda8a2ffa0ef93983675621892fceb7dbef1da4",
        "0x2ed59d517a4d685c8edfc356980b0859a7b08ad4",
        "0xd61afedb44b099657ef3f7d862e0bfb7656afac8",
        "0x7b2353c81a98bc0a09d47d22ef7acae558986cfa",
      ]
      const leafNodes = allowlist.map((addr) => keccak256(addr));
      const merkleTree = new MerkleTree(leafNodes, keccak256, { sortPairs: true });
      const leaf = keccak256(address.toLowerCase());
      const proof = merkleTree.getProof(leaf).map(p => `0x${p.data.toString("hex")}`);
      setMerkleProof(proof);
    }, [address])

    return (
        <div className="main">
            <div className="section">
                <RainbowConnectButton />
            </div>

            {
                isWrongNetwork
                ? <div className="main">
                    <div className="btn btn-primary" onClick={handleSwitchNetwork}>Switch Network</div>
                </div>
                :<>
                    <div className="section application">
                        <h1>ETHEREUM POW NAME SERVICE</h1>
                        <div className="input-group">
                            <input className="value-input name-input" value={name} onChange={handleNameChange} />
                            <div className={`btn btn-primary${isSearchDisabled ? ' disabled' : ''}`} onClick={handleSearch}>Search</div>
                        </div>
                        {isChecked && <p>"{name}" is {isNameAvailable ? '' : 'Not '}available</p>}
                    </div>

                    <div className="section btn-group">
                        <div className={`btn btn-primary${!isNameAvailable ? ' disabled' : ''}`} onClick={handleCommit}>Commit</div>
                        <div className={`btn btn-primary${isCommiting || !params.length ? ' disabled' : ''}`} onClick={handleRegister}>Register</div>
                    </div>

                    {isCommiting && <div className="loading">
                        <div>Please wait for 60 seconds</div>
                    </div>}
                    {Boolean(registration) && <div className="loading">
                        <div>{`Registration ${registration.success ? 'Success' : 'Failed'} for ${name}.`}</div>
                    </div>}
                </>
            }

            <div className="section settings">
                <div className="btn btn-secondary" onClick={handleToggleSettings}>Settings</div>
                {
                    openSettings &&
                    <>
                        <h3>Contract Address</h3>
                        <input className="value-input contract-input" value={contractAddr} onChange={handleAddressChange} />
                        <h3>Merkle Proof</h3>
                        <input className="value-input proof-input" value={JSON.stringify(merkleProof)} onChange={handleProofChange} />
                        <p>Example: ['0x0e9aadde361b85c8db3e1f3fd27bb12419b6ab13deb71cf063b7650186fec9a2']</p>
                        <h3>Resolver Address</h3>
                        <input className="value-input resolver-input" value={resolverAddr} onChange={handleResolverAddressChange} />
                        <h3>ChainId</h3>
                        <input className="value-input chainid-input" value={chainId} onChange={handleChainIdChange} />
                        <p>Example: 1 4 10001 </p>
                    </>
                }
            </div>
            
        </div>
    )
};

export default Main;